import { BoxGeometry, MeshPhongMaterial, Mesh, Vector3 } from "three"
import TextureRepository from "./loaders/TextureRepository"
export default class Tile {
    constructor(level, x, z, fruit) {
        this.level = level
        this.x = x
        this.z = z
        this.fruit = fruit
        this.tileSize = this.level.tileSize
        this.canShake = false
        this.shakeTime=0
        this.init()
    }
    init() {
        const geometry = new BoxGeometry(this.tileSize - 10, 20, this.tileSize - 10)
        const materials = []

        const textureNames = [
            'soil',
            'soil',
            'grass',
            this.fruit,
            'soil',
            'soil'
        ]

        for (const textureName of textureNames) {
            const texture = TextureRepository.getTexture(textureName)
            materials.push(new MeshPhongMaterial({
                map: texture,
                specular: 0xffffff,
                shininess: 2,
            }))
        }

        this.mesh = new Mesh(geometry, materials)
        this.mesh.position.x = this.x * this.tileSize + this.tileSize / 2
        this.mesh.position.z = this.z * this.tileSize + this.tileSize / 2
        this.mesh.position.y = -10
        this.startPosition = new Vector3(this.mesh.position.x, this.mesh.position.y, this.mesh.position.z)
        this.mesh.receiveShadow = true
    }

    randomVector() {
        let scale = 5
        let x = (Math.random() - 0.5) * scale
        let y = 0
        let z = (Math.random() - 0.5) * scale
        let vector = new Vector3(x, y, z)

        return vector
    }

    shake(delta) {
        this.shakeTime += delta
        let dir = this.randomVector()
        let toMove = this.startPosition.clone().add(dir.clone())
        this.mesh.position.set(toMove.x, toMove.y, toMove.z)
        if (this.shakeTime > 2) {
            this.mesh.rotation.x = this.idealRotation
            this.canShake = false
            this.mesh.position.set(this.startPosition.x, this.startPosition.y, this.startPosition.z)
            this.shakeTime = 0
        }
    }

    shakeRequest(rotation) {
        this.idealRotation = rotation
        this.canShake = true
    }

    update(delta) {
        if (this.canShake) {
            this.shake(delta)
        }
    }
}