import { BoxGeometry, Mesh, MeshPhongMaterial} from "three"
import TextureRepository from "./loaders/TextureRepository"

export default class Pavement {
    constructor(level, i, j) {
        this.level = level
        this.i = i
        this.j = j
        this.init()
    }

    init() {
        let geometry = new BoxGeometry(this.level.tileSize, 20, this.level.tileSize)
        let material = new MeshPhongMaterial({ map: TextureRepository.getTexture("pavement2"), specular: 0xffffff, shininess: 0.5, })
        this.mesh = new Mesh(geometry, material)
        this.mesh.position.x = this.i * this.level.tileSize + this.level.tileSize / 2
        this.mesh.position.z = this.j * this.level.tileSize + this.level.tileSize / 2
        this.mesh.position.y = -10
        this.mesh.receiveShadow = true
    }
}