import { Scene, Vector3,Clock} from 'three'
import Renderer from './Renderer'
import Camera from './Camera'
import Communication from "./Communication"
import Level from "./Level"
import Keyboard from "./Keyboard"
import ControlledPlayer from "./ControlledPlayer"
import Skybox from './Skybox'
import Logo from './Logo'
import Lights from './Lights'
import StartMenu from "./menus/StartMenu.js"
import LoginMenu from "./menus/LoginMenu.js"
import GameoverMenu from "./menus/GameoverMenu.js"
import HighScoreMenu from "./menus/HighScoreMenu.js"


export default class Main {
    constructor(container) {
        this.keyboard = new Keyboard()
        this.container = container
        this.scene = new Scene()
        this.renderer = new Renderer(this.container)
        this.camera = new Camera(45, window.innerWidth, window.innerHeight)
        this.camera.canRotate = false
        this.cameraAngle = 0
        this.levelReady = false
        this.players = []
        this.logo = new Logo(this.scene)
        this.skybox = new Skybox(this.scene)
        this.lights = new Lights(this.scene)
        this.clock = new Clock()
        this.communication = new Communication(this)
        this.createMenu()
        this.getLevel()
        this.render()
    }

    createMenu() {
        this.menus = {}
        this.menus.start = new StartMenu(this)
        this.menus.login = new LoginMenu(this)
        this.menus.over = new GameoverMenu(this)
        this.menus.score=new HighScoreMenu(this)
        this.currentMenu = "start"
        this.menus[this.currentMenu].show()
    }

    switchMenu(current) {
        this.menus[this.currentMenu].hide()
        this.currentMenu = current
        this.menus[this.currentMenu].show()
    }

    async getLevel() {
        this.levelGrid = await this.communication.getBoard()
        this.level = new Level(this, this.levelGrid)
        this.camera.canRotate = true
        this.levelReady = true
    }

    cameraRotationAnimation() {
        if (this.camera.canRotate) {
            this.cameraAngle += 0.01
            let offx = ((this.level.x + 2) * this.level.tileSize) / 2
            let offz = ((this.level.z + 2) * this.level.tileSize) / 2
            this.camera.position.x = Math.sin(this.cameraAngle) * 1200 + offx
            this.camera.position.z = Math.cos(this.cameraAngle) * 1200 + offz
            this.camera.lookAt(new Vector3(offx, 0, offz))
        }
    }

    async recreateLevel() {
        this.levelGrid = await this.communication.getBoard()
        this.createNextLevel(this.levelGrid)
    }

    createNextLevel(level) {

        this.level.levelObjects.forEach(obj => {
            this.scene.remove(obj)
        })
        this.level = new Level(this, level)
        this.players.forEach(pl => {
            pl.moveToStart()
        })
        this.setCameraInCorrectPoint()
    }

    getPlayerById(playerId) {
        return this.players.find(player => player.id == playerId)
    }

    setCameraInCorrectPoint() {
        let offx = ((this.level.x + 2) * this.level.tileSize) / 2
        let offz = ((this.level.z + 2) * this.level.tileSize) / 2

        this.camera.position.set(offx * 4, offx * 2.5, offx * 2.5)
        this.camera.lookAt(new Vector3(offx, 0, offz))
    }

    joinToGame(player) {
        this.recreateLevel()
        this.setCameraInCorrectPoint()
        this.camera.canRotate = false
        this.me = new ControlledPlayer(this, player.name, player.id)
        this.players.push(this.me)
    }

    clearScene() {
        this.players.forEach(player => {
            this.scene.remove(player.model)
            this.scene.remove(player.nameMesh)
        })
        this.recreateLevel()
    }

    resetGame() {
        this.switchMenu("start")
        this.clearScene()
        this.players = []
        this.me = undefined
        this.camera.canRotate = true
    }

    endGame(data) {
        document.getElementById("timer").remove()
        document.getElementById("points").remove()
        let con = document.getElementById("container")
        con.style.display = "block"
        this.switchMenu("over")
        this.menus[this.currentMenu].addPoints(data.points)
        if (data.reason != "no lives") {
            this.menus[this.currentMenu].addError("Other player left the game")
        }
    }

    update(delta) {
        this.cameraRotationAnimation()
        if (this.levelReady)
            this.level.update(delta)
        for (const player of this.players) {
            player.update(delta)
        }
    }

    render = () => {
        this.update(this.clock.getDelta())
        this.renderer.render(this.scene, this.camera)

        requestAnimationFrame(this.render)
    }
}