import { Object3D, SpriteMaterial, AdditiveBlending, PointLight } from "three"
import Particle from "./Particle"
import TextureRepository from "./loaders/TextureRepository"

export default class Fireplace extends Object3D {

    constructor() {
        super()
        this.particles = []
        this.count = 400
        this.particleMaterial = new SpriteMaterial({
            color: 0xffffff,
            map: TextureRepository.getTexture("fire"),
            transparent: true,
            opacity: 0.8,
            depthWrite: false,
            blending: AdditiveBlending,
        });
        this.point = new PointLight(0xe25822, 30, 30)
        this.init()
    }

    init() {
        for (let i = 1; i <= this.count; i++) {
            var particle = new Particle(this.particleMaterial)
            this.add(particle)
            this.particles.push(particle);
        }
    }

    update() {
        for (let p of this.particles) {
            p.update()
        }
        this.children.pop()
    }
}