export default class HighScoreMenu {
    constructor(game) {
        this.game = game
        this.communication = this.game.communication
        this.init()
        this.hide()
    }

    init() {
        this.menu = document.getElementById("highscoreMenu")
        this.records = this.menu.children[1]
        this.button = this.menu.children[2]
        this.button.addEventListener("click", () => {
            this.game.switchMenu("start")
        })
    }
    hide() {
        this.menu.style.display = "none"
    }

    makeTable(records) {

        console.log(records)
        if (records.length < 1) {
            this.addBlanc()
            return
        }

        this.makeHeading()

        for (let i = 0; i < records.length; i++) {
            this.makeRecord(records[i], i + 1)
        }
    }

    addBlanc() {
        let recordDiv = document.createElement("tr")
        let record = document.createElement("th")
        record.className = "blancRecord"
        record.innerText = "No records"
        recordDiv.appendChild(record)
        this.records.appendChild(recordDiv)
    }

    makeHeading() {
        let recordDiv = document.createElement("tr")
        recordDiv.className = "recordHeading record"

        let recordNumber = document.createElement("th")
        recordNumber.className = "recordNumber"
        recordNumber.innerText = "Lp"

        let user1 = document.createElement("th")
        user1.className = "player1"
        user1.innerText = "Players"
        user1.colSpan = "2"

        // let user2 = document.createElement("th")
        // user2.className = "player2"
        // user2.innerText = "Player2"

        let points = document.createElement("th")
        points.className = "points"
        points.innerText = "Points"

        let level = document.createElement("th")
        level.className = "level"
        level.innerText = "Level"

        recordDiv.appendChild(recordNumber)
        recordDiv.appendChild(user1)
        //  recordDiv.appendChild(user2)
        recordDiv.appendChild(points)
        recordDiv.appendChild(level)

        this.records.appendChild(recordDiv)
    }

    makeRecord(record, i) {
        let recordDiv = document.createElement("tr")
        recordDiv.className = "record"

        if (i == 1)
            recordDiv.classList.add("first")
        else if (i == 2)
            recordDiv.classList.add("second")
        else if (i == 3)
            recordDiv.classList.add("third")

        let recordNumber = document.createElement("td")
        recordNumber.className = "recordNumber"
        recordNumber.innerText = i

        let user1 = document.createElement("td")
        user1.className = "player1"
        user1.innerText = record.player1

        let user2 = document.createElement("td")
        user2.className = "player2"
        user2.innerText = record.player2

        let points = document.createElement("td")
        points.className = "points"
        points.innerText = record.points

        let level = document.createElement("td")
        level.className = "level"
        level.innerText = record.level

        recordDiv.appendChild(recordNumber)
        recordDiv.appendChild(user1)
        recordDiv.appendChild(user2)
        recordDiv.appendChild(points)
        recordDiv.appendChild(level)

        this.records.appendChild(recordDiv)
    }

    async show() {
        this.menu.style.display = "flex"
        let r = await this.communication.getRecords()
        this.records.innerHTML = ""
        this.makeTable(r)

    }
}