import { SkeletonUtils } from 'three/examples/jsm/utils/SkeletonUtils'

export default class ModelRepository {
    static models = new Map()

    static putModel(name, model) {
        ModelRepository.models.set(name, model)
    }

    static setModels(models) {
        ModelRepository.models = models
    }

    static getModel(name) {
        return ModelRepository.models.get(name)
    }

    static cloneModel(name) {
        return SkeletonUtils.clone(ModelRepository.getModel(name))
    }
}
