import ModelRepository from "./loaders/ModelRepository"
import FontRepository from "./loaders/FontRepository"
import { Mesh, TextGeometry, MeshBasicMaterial, MathUtils, AnimationMixer, LoopOnce, Vector3, Ray, Raycaster } from "three"
import Direction from "./Direction"
import AnimationRepository from "./loaders/AnimationRepository"

export default class Player {
    constructor(game, name, id) {
        this.game = game
        this.scene = this.game.scene
        this.name = name
        this.id = id
        this.idealRotationY = Direction.Down
        this.currentAnimation = null
        this.animations = {}
        this.init()
    }

    init() {
        this.model = ModelRepository.cloneModel('timmy')
        this.model.scale.set(1, 0.7, 1)
        for (const child of this.model.children) {
            child.castShadow = true
        }
        this.scene.add(this.model)

        this.mixer = new AnimationMixer(this.model)

        const animationNames = ['stand', 'walk', 'praying', 'sad', 'happy', 'death']

        for (const animationName of animationNames) {
            const clip = AnimationRepository.getAnimation(animationName)

            if (!clip) {
                throw 'Za animacja !!!'
            }

            const action = this.mixer.clipAction(clip)
            action.name = animationName
            this.animations[animationName] = action

        }

        const font = FontRepository.getFont('robotoLight')

        let nameGeo = new TextGeometry(this.name, {
            font: font,
            size: 150,
            height: 1,
            curveSegments: 12,
            bevelEnabled: true,
            bevelThickness: 10,
            bevelSize: 8,
            bevelOffset: 0,
            bevelSegments: 5
        });

        nameGeo.center()
        let nameMaterial = new MeshBasicMaterial({ color: 0x000000 })
        this.nameMesh = new Mesh(nameGeo, nameMaterial)

        this.nameMesh.position.y = 130
        this.nameMesh.rotation.y = Math.PI / 4
        this.nameMesh.scale.set(.1, .1, .1)

        this.scene.add(this.nameMesh)
        this.moveToStart()

        this.playAnimation('stand')

        this.mixer.addEventListener("finished", () => {
            if (this.currentAnimation.name == "praying") {
                this.playAnimation("stand")
            }
        })

    }

    moveToStart() {
        let x = this.id * this.game.level.tileSize - this.game.level.tileSize / 2
        let z = this.game.level.tileSize / 2
        this.setPosition(x, z)
        this.setRotation(0)
    }

    setPosition(x, z) {
        this.x = x
        this.z = z
        this.model.position.x = this.x
        this.model.position.z = this.z
        this.nameMesh.position.x = this.x
        this.nameMesh.position.z = this.z
    }

    setRotation(dir) {
        this.idealRotationY = dir
    }

    playAnimation(name, { timeScale = 1 } = {}) {
        const previous = this.currentAnimation
        const current = this.animations[name]

        if (!current) {
            throw new Error(`Unknown animation: ${name}`)
        }

        if (previous) {
            current.enabled = true
            current.time = 0
            this.mixer.timeScale = timeScale
            current.setEffectiveWeight(1)
            current.crossFadeFrom(previous, 0.5, true)
        }

        if (name == "death") {
            current.clampWhenFinished = true
            current.setLoop(LoopOnce, 1)

            let raycaster = new Raycaster()
            const dir = new Vector3(0, 0, -1)
            dir.transformDirection(this.model.matrixWorld)
            let ray = new Ray(this.model.position, dir)
            raycaster.ray = ray

            let intersects = raycaster.intersectObjects(this.game.level.walls);
            let distance=170
            if (intersects[0]?.distance < distance) {
                this.model.translateZ(distance - intersects[0].distance)
            }
        }
        current.play()
        this.currentAnimation = current

    }

    update(delta) {
        this.mixer.update(delta)
        this.model.rotation.y = MathUtils.lerp(this.model.rotation.y, this.idealRotationY, 0.25)
        this.nameMesh.lookAt(this.game.camera.position)
    }
}
