export default class LoginMenu {
    constructor(game) {
        this.game = game
        this.communication = this.game.communication
        this.init()
        this.hide()
    }

    init() {
        this.container = document.getElementById("container")
        this.menu = document.getElementById("loginMenu")
        this.input = this.menu.children[1]
        this.errorDiv = this.menu.children[2]
        this.button = this.menu.children[3]
        this.button.addEventListener("click", () => {
            this.sendLogin()
        })
    }

    async sendLogin() {
        let name = this.input.value
        if (name.length < 3) {
            this.errorDiv.innerText = "Your nickname is too short"
            return
        }
        let res = await this.communication.sendRequest("startGame", { name })
        if (res.error) {
            this.addError(res.error)
        } else {
            this.container.style.display = "none"
            this.communication.port = res.port
            this.input.value = ""
            this.communication.addTimerDiv()
            this.communication.addPointsDiv()
            this.game.joinToGame(res.player)
            this.communication.openWebSocket(res.player.id)
        }
    }
    hide() {
        this.menu.style.display = "none"
    }
    show() {
        this.menu.style.display = "flex"
    }
    addError(error) {
        this.errorDiv.innerText = error
    }
}