export default class StartMenu {
    constructor(game) {
        this.game = game
        this.init()
        this.hide()
    }
    init() {
        this.menu = document.getElementById("startMenu")
        this.startButton = this.menu.children[1]
        this.startButton.addEventListener("click", () => {
            this.game.switchMenu("login")
        })
        this.highscoreButton = document.getElementById("highscoreButton")
        this.highscoreButton.addEventListener("click", ()=>{
            this.game.switchMenu("score")
        })

    }
    hide() {
        this.menu.style.display = "none"
    }
    show() {
        this.menu.style.display = "flex"
    }
}
