export default class TextureRepository {
    static textures = new Map()

    static putTexture(name, texture) {
        TextureRepository.textures.set(name, texture)
    }

    static setTextures(textures) {
        TextureRepository.textures = textures
    }

    static getTexture(name) {
        return TextureRepository.textures.get(name)
    }
}
