import { BoxGeometry, Mesh, MeshPhongMaterial } from "three"
import TextureRepository from "./loaders/TextureRepository"

export default class Walls {
    constructor(level) {
        this.level = level
        this.scene = this.level.scene
        this.tileSize = this.level.tileSize
        this.wallThickness = this.level.wallThickness
        this.x = this.level.x
        this.z = this.level.z
        this.walls = this.level.walls
        this.createWalls()
    }

    createWalls() {
        //top-right
        let x = ((this.x + 2) / 2) * this.tileSize
        let z = -this.wallThickness / 2
        let width = (this.x + 2) * this.tileSize + 2 * this.wallThickness
        let depth = this.wallThickness
        this.createWall(x, z, width, depth)

        //bottom-right
        x = (this.x + 2) * this.tileSize + this.wallThickness / 2
        z = ((this.z + 2) / 2) * this.tileSize
        width = this.wallThickness
        depth = (this.z + 2) * this.tileSize
        this.createWall(x, z, width, depth)

        //bottom-left
        x = ((this.x + 2) / 2) * this.tileSize
        z = (this.z + 2) * this.tileSize + this.wallThickness / 2
        width = (this.x + 2) * this.tileSize + 2 * this.wallThickness
        depth = this.wallThickness
        this.createWall(x, z, width, depth)

        //top-left
        x = -this.wallThickness / 2
        z = ((this.z + 2) / 2) * this.tileSize
        width = this.wallThickness
        depth = (this.z + 2) * this.tileSize
        this.createWall(x, z, width, depth)
    }

    createWall(x, z, width, depth) {
        let geometry = new BoxGeometry(width, 100, depth)
        let wallMaterial = new MeshPhongMaterial({ map: TextureRepository.getTexture("wall"), specular: 0xffffff, shininess: 0.5, })
        let wall = new Mesh(geometry, wallMaterial)
        wall.position.x = x
        wall.position.y = 50
        wall.position.z = z
        wall.receiveShadow = true
        wall.castShadow = true
        this.walls.push(wall)
        this.level.levelObjects.push(wall)
        this.scene.add(wall)
    }
}