import Player from "./Player"

export default class Connection {
    constructor(game) {
        this.game = game
    }


    getBoard() {
        return this.sendRequest("getBoard", {})
    }

    getRecords() {
        return this.sendRequest("getRecords", {})
    }

    async sendRequest(link, value) {

        const body = JSON.stringify(value)
        const headers = { "Content-Type": "application/json" }

        let response = await fetch(`/${link}`, { method: "post", body, headers })

        if (!response.ok) {
            return response.status
        } else {
            let res = await response.json()
            return res
        }
    }

    openWebSocket(playerId) {
        let socket = new io()
        socket.on("connect", () => {
            this.socket = socket
            socket.emit("assignPlayer", { playerId })
            socket.on("message", (packet) => {
                this.handlePacket(packet)
            })
        })
    }

    handlePacket(packet) {
        switch (packet.type) {
            case 'world-state': {
                const { players } = packet.data
                for (const playerData of players) {
                    if (playerData.id != this.game.me.id) {
                        this.spawnPlayer(playerData)
                    }
                }
                break
            }
            case 'player-move': {
                const { playerId, x, z, rotation } = packet.data
                const player = this.game.getPlayerById(playerId)
                if (!player) break
                player.setPosition(x, z)
                player.setRotation(rotation)
                break
            }
            case 'player-animation': {
                const { playerId, animation, timeScale } = packet.data
                const player = this.game.getPlayerById(playerId)
                if (!player) break
                player.playAnimation(animation, { timeScale })
                break
            }
            case 'players-animation': {
                const { animation, timeScale } = packet.data
                this.game.players.forEach(player => {
                    player.playAnimation(animation, { timeScale })
                });
                break
            }
            case 'player-join': {
                this.spawnPlayer(packet.data.player)
                break
            }
            case 'countdown': {
                this.updateTimer(packet.data.number)
                break
            }
            case "rotate-tile": {
                const { rotation, tile1, tile2 } = packet.data
                let t1 = this.game.level.findTile(tile1)
                let t2 = this.game.level.findTile(tile2)
                t1.shakeRequest(rotation)
                t2.shakeRequest(rotation)
                break;
            }
            case "rotate-all": {
                const { rotation } = packet.data
                this.game.level.tiles.forEach(tile => {
                    tile.shakeRequest(rotation)
                })
                break;
            }
            case 'next-level': {
                const { level } = packet.data
                this.game.createNextLevel(level)
                break
            }
            case "lose-heart": {
                const { live } = packet.data
                this.game.level.loseHeart(live)
                break;
            }
            case "move-blocker": {
                const { canMove } = packet.data
                this.game.me.canMove = canMove
                break;
            }
            case "points-number": {
                this.updatePoints(packet.data.points)
                break;
            }
            case "end-game": {
                this.socket.disconnect()
                this.game.endGame(packet.data)
                break;
            }
        }
    }

    addPointsDiv() {
        let div = document.createElement("div")
        div.id = "points"
        div.innerText = "Score: 0"
        document.body.appendChild(div)
    }

    addTimerDiv() {
        let div = document.createElement("div")
        div.id = "timer"
        document.body.appendChild(div)
    }

    updatePoints(points) {
        let pointsDiv = document.getElementById("points")
        pointsDiv.innerText = "Score: " + points
        pointsDiv.classList.add("activePoints")
        setTimeout(() => {
            pointsDiv.classList.remove("activePoints")
        }, 400)
    }

    updateTimer(value) {
        let timer = document.getElementById("timer")
        timer.innerText = value
        timer.classList.add("activeTimer")
        setTimeout(() => {
            timer.classList.remove("activeTimer")
        }, 400)
    }

    spawnPlayer(playerData) {
        const player = new Player(
            this.game,
            playerData.name,
            playerData.id
        )
        player.setPosition(playerData.x, playerData.z)
        player.setRotation(playerData.rotation)
        this.game.players.push(player)
        return player
    }

    sendPacket(type, data) {
        if (this.socket) {
            this.socket.send(JSON.stringify({ type, data }))
        }
    }
}