import Heart from "./Heart"
import Pavement from "./Pavement"
import Tile from "./Tile"
import Walls from "./Walls"

export default class Level {
    constructor(game, grid) {
        this.game = game
        this.scene = this.game.scene

        this.x = grid.length
        this.z = grid[0].length
        this.wallThickness = 50
        this.tileSize = 100
        this.walls = []
        this.pavements = []
        this.tiles = []
        this.levelObjects = []
        this.hearts = []
        this.init(grid)
    }

    init(grid) {
        new Walls(this)
        this.createFloor(grid)
        this.createHearts()
    }

    createHearts() {
        for (let i = 0; i < 3; i++) {
            let heart = new Heart(this.game, this, i)
            this.hearts.push(heart)
        }
    }

    loseHeart(index) {
        this.hearts[index].burn()
    }

    createFloor(grid) {
        for (let i = 0; i < this.x + 2; i++) {
            for (let j = 0; j < this.z + 2; j++) {
                if (i == 0 || j == 0 || i == this.x + 1 || j == this.z + 1) {
                    this.createPavement(i, j)
                } else {
                    this.createTile(i, j, grid[i - 1][j - 1])
                }
            }
        }
    }

    createPavement(i, j) {
        let pav = new Pavement(this, i, j)
        this.scene.add(pav.mesh)
        this.pavements.push(pav)
        this.levelObjects.push(pav.mesh)
    }

    createTile(i, j, fruit) {
        let tile = new Tile(this, i, j, fruit)
        this.scene.add(tile.mesh)
        this.tiles.push(tile)
        this.levelObjects.push(tile.mesh)
    }

    findTile(coords) {
        return this.tiles.find(tile => tile.z - 1 == coords.z && tile.x - 1 == coords.x)
    }

    update(delta) {
        this.hearts.forEach(heart => {
            heart.update(delta)
        })

        this.tiles.forEach(tile => {
            tile.update(delta)
        })
    }
}
