import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'

export default class ModelFBXLoader {
    constructor(path, assetMap) {
        this.path = path
        this.assetMap = assetMap
        this.loader = new FBXLoader()
        this.assetsCount = Object.values(this.assetMap).length;
    }

    loadAssets() {
        return new Promise((resolve, reject) => {
            this.loadedAssets = new Map()

            const assets = Object.entries(this.assetMap)
            if (assets.length === 0) return resolve()

            let loaded = 0

            for (const [assetName, assetPath] of assets) {
                const assetUrl = `${this.path}/${assetPath}`

                const onLoad = object => {
                    this.loadedAssets.set(assetName, object)

                    if (++loaded === this.assetsCount) {
                        resolve()
                    }
                }

                const onError = event => {
                    reject(event)
                }

                this.loader.load(assetUrl, onLoad, undefined, onError)
            }
        })
    }
}
