import ModelRepository from "./loaders/ModelRepository"
import { Mesh, PlaneGeometry, MeshBasicMaterial, DoubleSide } from "three"
import Fireplace from "./Fireplace"
import TextureRepository from "./loaders/TextureRepository"
export default class Heart {
    constructor(game, level, id) {
        this.game = game
        this.level = level
        this.scene = this.game.scene
        this.id = id
        this.init()
    }
    init() {

        this.model = ModelRepository.cloneModel("heart")
        this.model.children[0].material = this.model.children[0].material.clone()
        this.model.children[0].material.transparent = true
        this.model.position.x = this.id * 100 + 50
        this.model.position.z = -25
        this.model.position.y = 130
        this.model.scale.set(0.3, 0.3, 0.5)
        this.level.levelObjects.push(this.model)
        this.scene.add(this.model)
        this.addShadow()
    }

    addShadow(){
        let shadowTexture = TextureRepository.getTexture("shadow")
        let shadowMaterial = new MeshBasicMaterial({
            side: DoubleSide,
            map: shadowTexture,
            transparent: true,
            opacity: 0.3,
            color: 0x000000
        })
        let shadowGeometry = new PlaneGeometry(50, 50)
        this.shadow = new Mesh(shadowGeometry, shadowMaterial)
        this.shadow.rotation.x = Math.PI / 2
        this.shadow.position.set(this.model.position.x, 101, this.model.position.z)
        this.level.levelObjects.push(this.shadow)
        this.scene.add(this.shadow)
    }

    update(delta) {
        this.model.rotation.y += 0.03
        if (this.fireplace) {
            this.startBurn += delta
            switch (this.burnState) {
                case "opacity": {
                    this.model.children[0].material.opacity -= 0.02
                    if (this.startBurn > 1)
                        this.burnState = "shadow"
                    break
                }
                case "shadow": {
                    this.addFireShadow()
                    this.scene.remove(this.shadow)
                    this.burnState = "remove"
                    break
                }
                case "remove": {
                    this.scene.remove(this.fireplace)
                    this.level.hearts.pop()
                    break
                }
            }

            this.fireplace.update()
        }
    }

    addFireShadow() {
        let shadowTexture = TextureRepository.getTexture("fire")
        let shadowMaterial = new MeshBasicMaterial({
            side: DoubleSide,
            map: shadowTexture,
            transparent: true,
            opacity: 0.8,
            color: 0x000000
        })
        let shadowGeometry = new PlaneGeometry(50, 50)
        let shadow = new Mesh(shadowGeometry, shadowMaterial)
        shadow.rotation.x = Math.PI / 2
        shadow.position.set(this.model.position.x, 101, this.model.position.z)
        this.scene.add(shadow)
        this.level.levelObjects.push(shadow)
    }

    burn() {
        this.startBurn = 0
        this.burnState = "opacity"
        this.fireplace = new Fireplace()
        this.fireplace.position.y = 130
        this.fireplace.position.z = -25
        this.fireplace.position.x = this.id * 100 + 50
        this.scene.add(this.fireplace)
    }
}