import Player from "./Player"
import { Raycaster, Ray, Vector3 } from "three"
import Direction from "./Direction"
import Keyboard from "./Keyboard"

export default class ControlledPlayer extends Player {

  constructor(game, name, playerId) {
    super(game, name, playerId)
    this.speed = 200
    this.raycaster = new Raycaster()
    this.canMove=true
  }

  update(delta) {
    super.update(delta)

    if(this.canMove){
      if (Keyboard.isKeyDown('KeyW')) {
        this.setRotation(Direction.Up)
        let distance = this.speed * delta
        this.moveForward(distance)
      } else if (Keyboard.isKeyDown('KeyD')) {
        this.setRotation(Direction.Right)
        let distance = this.speed * delta
        this.moveForward(distance)
      } else if (Keyboard.isKeyDown('KeyS')) {
        this.setRotation(Direction.Down)
        let distance = this.speed * delta
        this.moveForward(distance)
      } else if (Keyboard.isKeyDown('KeyA')) {
        this.setRotation(Direction.Left)
        let distance = this.speed * delta
        this.moveForward(distance)
      } else {
        if (this.currentAnimation.name == "walk") {
          this.playAnimation("stand")
          this.sendAnimation()
        }
      }
    }
  }

  moveForward(distance) {
    if (!this.collision()) {
      this.model.translateZ(distance)
      let x = this.model.position.x
      let z = this.model.position.z
      this.setPosition(x, z)
      this.sendMove()
      if (this.currentAnimation.name != "walk") {
        this.playAnimation("walk", { timeScale: 2.1 })
        this.sendAnimation()
      }
    }
  }

  sendMove() {
    this.game.communication.sendPacket('player-move', {
      x: this.x,
      z: this.z,
      rotation: this.idealRotationY
    })
  }

  sendAnimation() {
    this.game.communication.sendPacket('player-animation', {
      animation: this.currentAnimation.name,
      timeScale: this.mixer.timeScale
    })
  }

  collision() {
    const dir = new Vector3(0, 0, 1)
    dir.transformDirection(this.model.matrixWorld)
    let ray = new Ray(this.model.position, dir)
    this.raycaster.ray = ray

    let intersects = this.raycaster.intersectObjects(this.game.level.walls);

    let collide = false
    for (let i = 0; i < intersects.length; i++) {
      if (intersects[i].distance < 60) {
        collide = true
        break
      }
    }

    return collide
  }
}
