import { BoxGeometry, Mesh, MeshPhongMaterial, TextGeometry } from "three"
import FontRepository from "./loaders/FontRepository"

export default class Logo {
    constructor(scene) {
        this.scene = scene
        this.font = FontRepository.getFont('robotoLight')
        this.init()
    }

    init() {
        this.makeTitle()
        this.makeAuthor()
        this.makeBox()
    }

    makeTitle() {
        let geo = new TextGeometry("Memory", {
            font: this.font,
            size: 135,
            height: 10,
            curveSegments: 12,
            bevelEnabled: true,
            bevelThickness: 10,
            bevelSize: 8,
            bevelOffset: 0,
            bevelSegments: 5
        });

        geo.center()
        let material = new MeshPhongMaterial({
            color: 0x000000,
            specular: 0xffffff,
            shininess: 4
        })
        let mesh = new Mesh(geo, material)

        mesh.position.y = 190
        mesh.position.x = -25
        mesh.position.z = 300

        mesh.rotation.y = Math.PI / 2

        this.scene.add(mesh)
    }

    makeAuthor() {
        let geo = new TextGeometry("Moniowski   &   Sztefko", {
            font: this.font,
            size: 25,
            height: 10,
            curveSegments: 12,
        });

        geo.center()
        let material = new MeshPhongMaterial({
            color: 0x000000,
            specular: 0xffffff,
            shininess: 2
        })
        let mesh = new Mesh(geo, material)

        mesh.position.y = 118
        mesh.position.z = 275
        mesh.rotation.y = Math.PI / 2

        this.scene.add(mesh)
    }


    makeBox() {
        let geo = new BoxGeometry(20, 25, 143)

        let material = new MeshPhongMaterial({
            color: 0xff0000,
            specular: 0xffffff,
            shininess: 2
        })
        let mesh = new Mesh(geo, material)

        mesh.position.x = -20
        mesh.position.y = 118
        mesh.position.z = 569

        this.scene.add(mesh)
    }
}