export default class GameoverMenu {
    constructor(game) {
        this.game = game
        this.init()
        this.hide()
    }

    init() {
        this.menu = document.getElementById("gameoverMenu")
        this.errorDiv = this.menu.children[1]
        this.errorDiv.style.display = "none"
        this.points = this.menu.children[2]
        this.button = this.menu.children[3]
        this.button.addEventListener("click", () => {
            this.game.resetGame()
        })
    }

    addPoints(points) {
        this.points.innerText = "Score: " + points
    }

    hide() {
        this.menu.style.display = "none"
    }
    show() {
        this.menu.style.display = "flex"
    }
    addError(error) {
        this.errorDiv.style.display = "block"
        this.errorDiv.innerText = error
    }
}