import TextureLoader from "./TextureLoader"
import ModelFBXLoader from "./ModelFBXLoader"
import TextureRepository from "./TextureRepository"
import ModelRepository from "./ModelRepository"
import AnimationRepository from "./AnimationRepository"
import FontRepository from "./FontRepository"
import FontLoader from "./FontLoader"

export default class AssetsLoader {
    static async load() {

        const texturesToLoad = {
            fire:['fire.png'],
            shadow:['shadow.png'],
            grass: ['grass.jpg'],
            pavement1: ['pavement1.jpg'],
            pavement2: ['pavement2.jpg'],
            pavement3: ['pavement3.jpg'],
            pavement4: ['pavement4.jpg'],
            soil: ['soil.jpg', [1, 0.25]],
            wall: ['wall.jpg'],
            skyboxTop: ['skybox/top.jpg'],
            skyboxBottom: ['skybox/bottom.jpg'],
            skyboxRight: ['skybox/right.jpg'],
            skyboxLeft: ['skybox/left.jpg'],
            skyboxFront: ['skybox/front.jpg'],
            skyboxBack: ['skybox/back.jpg'],
            apple: ['fruits/apple.png'],
            banana: ['fruits/banana.png'],
            cherry: ['fruits/cherry.png'],
            coconut: ['fruits/coconut.png'],
            grapes: ['fruits/grapes.png'],
            kiwi: ['fruits/kiwi.png'],
            lemon: ['fruits/lemon.png'],
            lime: ['fruits/lime.png'],
            mango: ['fruits/mango.png'],
            orange: ['fruits/orange.png'],
            papaya: ['fruits/papaya.png'],
            peach: ['fruits/peach.png'],
            pear: ['fruits/pear.png'],
            pineapple: ['fruits/pineapple.png'],
            plum: ['fruits/plum.png'],
            raspberry: ['fruits/raspberry.png'],
            strawberry: ['fruits/strawberry.png'],
            watermelon: ['fruits/watermelon.png'],
        }

        const modelsFbxToLoad = {
            timmy: 'Timmy/Timmy.fbx',
            heart: 'Heart/Heart.fbx'
        }

        const animationsToLoad = {
            happy: 'Timmy/Happy.fbx',
            praying: 'Timmy/Praying.fbx',
            sad: 'Timmy/Sad.fbx',
            stand: 'Timmy/Stand.fbx',
            walk: 'Timmy/Walk.fbx',
            death: 'Timmy/Death.fbx'
        }

        const fontsToLoad = {
            robotoLight: 'Roboto-Light.json'
        }

        const textureLoader = new TextureLoader('/assets/textures', texturesToLoad)
        const modelFbxLoader = new ModelFBXLoader('/assets/models', modelsFbxToLoad)
        const animationLoader = new ModelFBXLoader('/assets/models', animationsToLoad)
        const fontLoader = new FontLoader('/assets/fonts', fontsToLoad)

        const loaders = [
            textureLoader,
            modelFbxLoader,
            animationLoader,
            fontLoader,
        ]

        await Promise.all(loaders.map(loader => loader.loadAssets()))

        TextureRepository.setTextures(textureLoader.loadedAssets)
        ModelRepository.setModels(modelFbxLoader.loadedAssets)

        for (const [name, model] of animationLoader.loadedAssets) {
            const animation = model.animations[0]
            AnimationRepository.putAnimation(name, animation)
        }

        FontRepository.setFonts(fontLoader.loadedAssets)

    }
}
