import { BackSide, BoxGeometry, Mesh, MeshBasicMaterial, Object3D } from "three";
import TextureRepository from "./loaders/TextureRepository";

export default class Skybox extends Object3D {
    constructor(scene) {
        super()

        const geometry = new BoxGeometry(5000, 5000, 5000)
        const materials = []

        const textureNames = [
            'skyboxRight',
            'skyboxLeft',
            'skyboxTop',
            'skyboxBottom',
            'skyboxFront',
            'skyboxBack',
        ]

        for (const textureName of textureNames) {
            const texture = TextureRepository.getTexture(textureName)
            materials.push(new MeshBasicMaterial({
                side: BackSide,
                map: texture
            }))
        }

        const mesh = new Mesh(geometry, materials)
        this.add(mesh)
        scene.add(this)
    }
}
