import { Sprite, Vector3 } from "three"
export default class Particle extends Sprite {
    constructor(material) {
        super()

        this.material = material.clone()
        this.scale.setScalar(20)
        this.start = new Vector3(0, 0, 0)

        this.position.set(0, 0, 0)
        this.vector = this.randomVector()
        this.opacityChange = Math.random() / 100


    }
    randomVector() {
        let x = Math.random() - 0.5
        let y = Math.random() - 0.5
        let z = Math.random() - 0.5
        let vector = new Vector3(x, y, z)

        return vector
    }

    update() {

        this.position.add(this.vector.clone())
        let distance = this.position.clone().distanceTo(this.start.clone())
        if (distance > 40) {
            this.material.opacity = 0
        }

        this.material.opacity -= this.opacityChange
    }
}