import { RepeatWrapping, TextureLoader as ThreeTextureLoader } from 'three'

export default class TextureLoader {
    constructor(path, assetMap) {
        this.path = path
        this.assetMap = assetMap
        this.loader = new ThreeTextureLoader()
        this.assetsCount = Object.values(this.assetMap).length;
    }

    loadAssets() {
        return new Promise((resolve, reject) => {
            this.loadedAssets = new Map()

            const assets = Object.entries(this.assetMap)
            if (assets.length === 0) return resolve()

            let loaded = 0

            for (const [assetName, assetOptions] of assets) {
                const [assetPath, assetScale] = assetOptions
                const assetUrl = `${this.path}/${assetPath}`

                const onLoad = texture => {
                    this.loadedAssets.set(assetName, texture)

                    if (assetScale) {
                        texture.repeat.set(...assetScale)
                    }

                    texture.wrapS = RepeatWrapping;
                    texture.wrapT = RepeatWrapping;

                    if (++loaded === this.assetsCount) {
                        resolve()
                    }
                }

                const onError = event => {
                    reject(event)
                }

                this.loader.load(assetUrl, onLoad, undefined, onError)
            }
        })
    }
}
